.Display {
  display: flex;
  flex-direction: column;
  width: 85%;
  margin: 2rem auto;
  padding: 3rem;
  background-color: white;
  border-radius: 1rem;
}

.Display .Results {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 3rem;
  font-size: 1.4rem;
}

.Display .Results .Impact {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.Display .Results .Impact div {
  margin: .5rem 0;
}

.Display .Impact .Title {
  font-size: 1.3rem;
}

@media (max-width: 768px) {
  .Display {
    width: 90%;
    padding: 2rem;
  }

  .Display .Results {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .Display .Results .Impact {
    margin-bottom: 2rem;
  }
}